'use strict';

import Inferno from 'inferno';
import Component from 'inferno-component';
import { Link, IndexLink } from 'inferno-router';
import styles from './app.css';

export default class App extends Component {
  constructor(props) {
    super(props);
    this.state = {};
    this.update = this.update.bind(this);
  }

  update(props) {
    this.setState({...this.state, ...props});
  }

  render() {
    const {children, params, ...initialState} = this.props;

    const Main = children? Inferno.cloneVNode(children, {
      ...initialState,
      ...this.state,
      update: this.update
    }) : null;

    return (
      <div>
        <div className={styles.left_column}>
          <header id="header">
            <div className={styles.profilepic}>
              <a />
            </div>
            <h1></h1>
            <p className={styles.subtitle}></p>
            <nav className={styles.main_nav}>
              <ul>
                <li><Link to="/" title=""></Link></li>
                <li><Link to="/tags" title=""></Link></li>
                <li><Link to="/archives" title=""></Link></li>
                {/*<li><Link to="/about" title=""></Link></li>*/}
              </ul>
            </nav>
          </header>
        </div>
        <div className={styles.main_column}>
          <div className={styles.content}>{Main}</div>
          <footer>
            <p> 2017 - <a href="https://github.com/Oscar-ren" target="_block">Oscar-ren</a> </p>
          </footer>
        </div>
      </div>
    )
  }
}

App.defaultProps = {
  //props
  origin: typeof window !== 'undefined' ? window.location.origin : ""
}


