'use strict';

import Component from 'inferno-component';
import { Link, IndexLink } from 'inferno-router';
import postListStyle from '../PostList/postlist.css';
import styles from './post.css';
import fecha from 'fecha';
// import 'github-markdown-css/github-markdown.css';

export default class Post extends Component {
  constructor(props) {
    super(props);
  }

  componentWillMount() {
    window.scrollTo(0, 0);
    this.getPostData(this.props.params.name);
  }

  componentDidUpdate() {
    const {title} = this.props.post;
    if(title) {
      document.title = title;
    }
  }

  getPostData(name) {
    const { posts, params } = this.props;
    for(let i = 0; i < posts.length; i++) {
      if(posts[i].name === name) {
        this.props.update({post: posts[i]});
      }
    }
    // fetch(`/json/post/?name=${encodeURIComponent(name)}`)
    //   .then(res => res.json())
    //   .then(data => {
    //     this.props.update(data);
    //   })
    //   .catch(err => {
    //   console.log(err);
    // })
  }

  render() {
    const {origin, post} = this.props;

    console.log(this.props);
    return (
      <div>
        <article>
          <section className={postListStyle.meta}>
            <div className={postListStyle.date}>{post.date ? fecha.format(new Date(post.date), "MMM D, YYYY") : ''}</div>
            <div>
              {post.tags && post.tags.map(tag => (
                <Link className={postListStyle.tag} to={`/tags#${tag}`}>{tag}</Link>
              ))}
            </div>
          </section>
          <h1 className="title">{post.title}</h1>
          <div dangerouslySetInnerHTML={{__html: post.content}} />
          <div className="copyright_info">
            <p><a href={`${origin}/blog/${post.name}`} target="_blank">{`${origin}/blog/${post.name}`}</a></p>
            <p><a href="http://creativecommons.org/licenses/by/4.0/deed.zh" target="_blank"> 4.0 </a></p>
          </div>
        </article>
      </div>
    )
  }
}

Post.defaultProps = {

}


